@import '~antd/dist/antd.css';

/*
side color - #eb7f34
side color 2 - #df4c73
*/
:root {
  --main-color: #eb7f34;
  --secondary-color: #df4c73;
}

.ant-layout-header {
  background-color: var(--main-color);
}

/*.ant-layout-header {
  background-color: #eb7f34  ;
  color: white;
  height: 50px;
  line-height: 50px;
}*/
body {
  font-family: 'Assistant', sans-serif;
  font-weight: 400;
  font-size: 16px;
}

.logo {
  font-size: 1.5rem;
  font-family: cursive, sans-serif;
  font-style: italic;
}

span.date {
  font-family: cursive, sans-serif;
}


.ant-layout-content {
  margin-top: 50px;
  min-height: 100vh;
  background-color: #e2f0f9;
  padding: 0px 50px;
  font-family: Assistant;
}

.ant-layout-footer {
  text-align: center;
  background-color: #1f305e;
  color: white;
  padding: 0.5em;
}

.logo {
  font-size: x-large;
  font-family: cursive;
  font-style: italic;
}

span.date {
  font-family: cursive;
}

#workshop-header {
  text-align: center;
  margin-bottom: 16px;
}

@media screen and (min-width: 768px) {
  #workshop-header {
    text-align: right;
  }  
}


#workshop-page .ant-image {
  display: unset;
}

.ant-timeline-item-content {
  margin: 0px 2em !important;  
}

.ant-space {
  grid-gap: 1em !important;
}

.personel-space > .ant-space-item {
  background-color: #eb7f34;
}

.ant-space-item .ant-card {
  max-width: 25em;
  margin: 0 0.5em 0 0;
  font-size: inherit !important;
}

.contact {
  background-color: #c7dbde;
  padding: 1%;
  margin-top: 1vh;
  border-radius: 15px;
  display: flex; 
  flex-wrap: wrap;
  justify-content: space-between;
}
.contact-button {
  background-color: var(--main-color);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 8px 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
img {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}img {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}
