@media screen and (max-width: 600px) {
  #top-row .ant-carousel .slick-slider {
    height: 200px !important;
  }
  #top-row .ant-carousel img {
    height: 200px !important;
  }
  .contact {
    padding: 2%;
  }
}

.col-1 {
  width: 75%;
} 

/* Extra small devices (phones 600px and up)*/
@media only screen and (max-width: 600px) {
  .col-1 {
    width: 100%;
  }

  .contact-button {
    margin-top: 10px;
  }
}

@media only screen and (min-width: 600px) and (max-width: 992px){
  .col-1 {
    width: 85%;
  } 
}

.ant-collapse-header svg {
  transform: rotate(180deg);
  margin-left: 1em;
}

.ant-back-top {
  left: 5vh;
  bottom: 8vh;
}

.ant-descriptions-divider {
  color: #eb7f34;
}

.ant-descriptions-view {
  background-color: #f1f1f1;
}

.ant-descriptions-view .ant-descriptions-item-label {
  background-color: #c7dbde;
  font-weight: bold;
}

.room-image {
  height: 12vh;
  width: 18vh;
}